<template>
    <div class="Application">
        <div class="Application-firstsection">
              <div class="Application-firstsection-text">
                  <p>{{ Applicationfirstsectiontextp }}</p>
                  <span>{{ Applicationfirstsectiontextspan }}</span>
              </div>
              <div class="Application-firstsection-image">
                   <img v-for="(imgcubescatter, index) in imagecubescatter" :src="imgcubescatter" v-bind:key="index" alt="cubescatter" />
              </div>

        </div>

        <div class="Application-secondsection">
           <div class="Application-secondsection-box">
              <svg viewBox="0 0 32 32" xmlns="http://www.w3.org/2000/svg"><path d="M7.712 20l4.467 9.828c.791 1.74 3.34 1.485 3.77-.378l4.191-18.163 1.928 7.228A2 2 0 0 0 24 20h6a2 2 0 1 0 0-4h-4.463L21.932 2.485C21.395.47 18.52.519 18.052 2.55L13.377 22.8l-2.557-5.628A2 2 0 0 0 9 16H2a2 2 0 1 0 0 4h5.712z" fill="#25C4F2" fill-rule="nonzero"/></svg>
              <p>{{ Metrics }}</p>
              <span>{{ Metricsdescription }}</span>
           </div>
           <div class="Application-secondsection-box">
            <svg viewBox="0 0 32 32" xmlns="http://www.w3.org/2000/svg"><g fill="none" fill-rule="evenodd"><path d="M4 0h24a4 4 0 0 1 4 4v24a4 4 0 0 1-4 4H4a4 4 0 0 1-4-4V4a4 4 0 0 1 4-4zm6.907 8.58c-.382-.829-1.586-.753-1.861.119l-6 19A1 1 0 0 0 4 29h24a1 1 0 0 0 .907-1.42l-6-13a1 1 0 0 0-1.667-.23l-4.986 5.816-5.347-11.585z" fill="#25C4F2"/><path d="M23.247 3.108c.792-.402 1.677.353 1.404 1.199-.098.305-.15.627-.15.959 0 1.562 1.14 2.837 2.57 2.938.87.062 1.25 1.132.612 1.728A3.91 3.91 0 0 1 25.003 11C22.782 11 21 9.128 21 6.844c0-1.6.88-3.042 2.247-3.736z" fill="#92E1F8" fill-rule="nonzero"/></g></svg>
              <p>{{ CloudFrontCDN }}</p>
              <span>{{ CloudFrontCDNdescription }}</span>
           </div>
           <div class="Application-secondsection-box">
             <svg viewBox="0 0 32 32" xmlns="http://www.w3.org/2000/svg"><path d="M16 32C7.163 32 0 24.837 0 16S7.163 0 16 0s16 7.163 16 16-7.163 16-16 16zm.99-29.7c-.123.158-.313.34-.612.592-.094.08-.184.153-.383.316-1.323 1.088-1.853 1.728-1.853 2.834 0 .77.307 1.262.907 1.796l.267.235c.406.364.53.608.507 1.221-.023.611-.23.985-.765 1.53-.036.035-.356.353-.452.453-.586.61-.89 1.155-.89 1.908 0 2.144 2.551 5.047 4.99 5.047 1.485 0 2.103-.754 2.735-2.43l.147-.39c.303-.785.542-1.152.905-1.351 1.256-.69 3.332-.577 4.54.02.344.17.214.092 2.201 1.302l.16.097a.34.34 0 0 0 .511-.339C28.74 7.164 24.486 2.8 17.147 2.046a1.49 1.49 0 0 1-.156.254zM16 30c.098.005.21.006.337.003a1 1 0 0 0 .96-1.193c-.43-2.184-1.983-3.847-4.66-4.99-.688-.28-2.724-.97-2.91-1.042-.464-.18-.696-.321-.682-.294-.111-.219-.102-.469.05-1.255.355-1.824.19-2.895-1.165-4.072-1.357-1.178-2.581-1.432-3.853-.835-.354.165-.692.383-1.125.705l-.323.244a1 1 0 0 0-.358 1.061C4.317 25.77 8.893 29.66 16 30z" fill="#25C4F2" fill-rule="evenodd"/></svg>
              <p>{{ DNSCertificates }}</p>
              <span>{{ DNSCertificatesdescription }}</span>
           </div>
        </div>

        <div class="screenshort">
            <img v-for="(imagescreenshort, index) in vapourscreenshort" :src="imagescreenshort" v-bind:key="index" alt="screenshort" />
        </div>

        
    </div>
</template>


<script>
export default {
    data(){
        return{
            Applicationfirstsectiontextp:"A robust platform for managing your entire Laravel application infrastructure.",
            Applicationfirstsectiontextspan:"We've sweat the small stuff and meticulously crafted Vapor into the perfect Laravel deployment platform.",
            Metrics:"Metrics",
            CloudFrontCDN:"CloudFront CDN",
            DNSCertificates:"DNS & Certificates",
            Metricsdescription:"Vapor monitors a variety of metrics about your applications, databases, and caches. You'll never lose sight of your application's health. We'll even alert you when performance isn't up to par.",
            CloudFrontCDNdescription:"During deployment, Vapor automatically uploads and serves your assets via S3 or CloudFront CDN for blazing-fast load times.",
            DNSCertificatesdescription:"You can manage your application's DNS records entirely from the Vapor UI or CLI. In addition, every Vapor application receives a free, auto-renewing SSL certificate during deployment.",

            imagecubescatter:["https://d2n6cmh8j179w8.cloudfront.net/f25bb57c-a0bd-4fe2-9d95-9dd17a86c032/img/desktop-cubes-scattered.png"],
            vapourscreenshort:["https://d2n6cmh8j179w8.cloudfront.net/77a9dcc0-a825-4776-8068-4f95df2c50d1/img/vapor-screenshot.png"]
        }
    }
}
</script>

<style scoped>
   .Application{
           background-color: #2c2f33;
           width: 100%;
           box-sizing: border-box;
           color:white;
           padding-top: 120px;
           
   }
  .Application-firstsection{
      width: calc(100% - 30vw);
      margin:0px auto 0px auto;
      display: flex;
      box-sizing: border-box;
      height:max-content
  }
  .Application-firstsection-text{
      width:55%;
      box-sizing: border-box;
      height: max-content;
      padding-top: 5vw;
  }
   .Application-firstsection-text p{
      font-size: 1.7vw;
       line-height: 2vw;
   }
   .Application-firstsection-text span{
            font-size: 1.3vw;
            color: #a1aebb;
            line-height: 1.5;
            margin-top: 1vw;
            display: inline-block;
   }
  .Application-firstsection-image{
      width:45%;
      box-sizing: border-box;
     margin-top: -11vw;
  }
  .Application-firstsection-image img{
      height: 100%;
      width: 100%;
      object-fit: cover;
      object-position: center;
       background-position: right top;
  }
  .Application-secondsection{
      width: calc(100% - 30vw);
      margin:0px auto 0px auto;
      display: flex;
      box-sizing: border-box;
  }
  .Application-secondsection p{
      color:white;
      font-size: 1.3vw;
      margin-top: 1vw;
  }
  .Application-secondsection span{
        color: #a1aebb;
        margin-top: 1vw;
        display: inline-block;
        line-height: 150%;   
        font-size: 1.1vw;
    }
    .Application-secondsection-box{
        padding: 0 1vw 0 1vw;
    }
    .Application-secondsection-box svg{
        width: 2vw;
        height:2vw;
    }
    .screenshort{
        width: calc(100% - 40vw);
         margin:0px auto 0px auto;
         overflow: hidden;
         transform: translateY(4vw);
         
    }
    .screenshort img{
        height: 100%;
        width: 100%;
        object-fit: cover;
        object-position: center;
    }
</style>


