</template>


<script>
export default {
    data(){
      return{
        bannerfeatutefirstsectiontextp:'On-demand auto-scaling with zero server maintenance.',
        bannerfeatutefirstsectiontextspan :"You have to see it to believe it. Watch your web application and workers scale within seconds based on demand.",
        bannerfeatutefirstsectiontextdtatbase:"Databases",
        bannerfeatutefirstsectiontextdtatbasespan:"Vapor isn't just about deployments. Create, manage, scale, and even restore your traditional and serverless databases directly from Vapor's intuitive dashboard.",
        Caches:"Caches",
        Cachesspan:"Need data fast? Vapor makes it a breeze to create and manage ElastiCache Redis clusters. Need even more speed? Let Vapor scale your cache while you kick back and relax.",
        servicefeatureheading:"A serverless future for Laravel.",
        servicefeaturedescription:"Laravel Vapor is fine-tuned by Laravel's creators to work seamlessly with the framework. You can keep writing your Laravel applications exactly like you're used to.",
        Queues:"Queues",
        Queuesdesciption:"Write and dispatch your Laravel jobs. Then, experience the power of serverless scalability as hundreds of jobs are executed concurrently with zero configuration.",
        CloudStorage:"Cloud Storage",
        CloudStoragedescription:"Easily stream your file uploads directly to S3 with Vapors built-in JavaScript utilities. Its like magic.",

