<style scoped>
.banner-feature{
    width:100%;
    background-color: #f4f6f8;
    padding: 5vw 0 3vw 0;
    margin-top: 3vw;
}
  .banner-feature-section{
      width: calc(100% - 20vw);
      display: flex;
      box-sizing: border-box;
      overflow: hidden;
      margin:0 auto 0 auto;
  }
  .banner-feature-section-boxes:nth-child(1){
    padding:3vw 3vw 0 0;
  }
  .banner-feature-section-boxes{
     overflow: hidden;
      
  }
  .banner-feature-section-boxes p{
     color: #2c2f33;
     font-size: 1.8vw;
     line-height: 135%;
  }
  .banner-feature-section-boxes span{
      color: #6e767e;
      font-size: 1.15vw;
      line-height: 120%;
      margin-top: 1.3vw;
      display: block;
  }
  
  .banner-feature-section-boxes img{
    height: 100%;
    width: 100%;
    background-size: cover;
    object-fit: cover;
    object-position: center;
  }
  .banner-feature-section-boxes-text p{
      font-size: 1.8vw;
  }
  .banner-feature-section-boxes-textimage-section{
      margin-top: 1.5vw;
      display: flex;
  }
  .banner-feature-section-boxes-text{
    margin-left: 1vw;
  }
  .box-text{
    width:50%;
    box-sizing: border-box;
  }
  .box-image{
    width:50%;
    box-sizing: border-box;
  }
  .box-image-cloudweb{
    width:55%;
  }
  .box-text-cloudweb{
    width:45%;
  }
  .cloudweb{
    margin-top: 5vw;
  }
  .banner-feature-section-boxes-image svg{
    width:2vw;
  }
</style>


