import Vue from 'vue'
import Router from 'vue-router'
import Homepage from '@/components/Homepage'
// import Login from '@/components/Login'
// import Sign_up from '@/components/Sign_up'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Homepage',
      component: Homepage
    },
    {
      path: '/login',
      name: 'Login',
      // component: Login
      component: () => import(/* Log in page   */ "../components/login")
    },
    {
      path: '/Sign_up',
      name: 'Sign up',
      // component: Sign_up
      component: () => import(/* Sign up page   */ "../components/Sign_up")
    }
  ],
  mode:"history"
  
})



